.sidebarItem {
  display: flex;
  padding: 10px 0;
  border-radius: 0 100px 100px 0;
}
.sidebarItem:hover {
  background-color: rgba(0, 0, 0, 0.06);
}
.sidebarItem__arrow {
  width: 28px;
  margin-left: 12px;
}
.sidebarItem__main {
  display: flex;
}
.sidebarItem__main > p {
  margin-left: 12px;
}
